/*4.0 Header Styling -------------------------------------------*/
.screenReaderSkipToContent{
	position: absolute;
	top: -100px;
	&:focus{
		left: 6px;
    top: 7px;
    height: auto;
    width: auto;
    display: block;
    font-size: 14px;
    font-weight: 600;
    padding: 15px 23px 14px;
    background: $primaryGrey;
    color: $primaryYellow;
    z-index: 100000;
    line-height: normal;
    text-decoration: none;
    box-shadow: 0 0 2px 2px rgba(0,0,0,.6);
	}
}
.site-header{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	height: 70px;
	width: 100vw;
	background-color: $primaryGrey;
}
.main-navigation{
	height: 100px;
	width: 100%;
	margin: 0 auto;
	background: $primaryGrey;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	box-shadow: 0 0 28px 0 rgba(0,0,0,.25);
		@include breakpoint(smallScreen){
			height: 70px;
		}
}
.secondaryNavigation{
	height: 25px;
	margin: 0 auto;
	width: 100%;
	background: $primaryGrey;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
#primary-menu{
	@include breakpoint(smallScreen){
		display: none;
	}
	@include breakpoint(largeMobileScreen){
		display: none;
	}
	@include breakpoint(laptopScreen){
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
	}
	li{
		padding: 0 15px;
		list-style-type: none;
		a{
			text-decoration: none;
			color: $white;
			font-weight: 700;
    	letter-spacing: 1px;
			padding: 5px;
			@include breakpoint(tabletScreen){
				font-size: 16px;
			}
			@include breakpoint(largeScreen){
				font-size: 20px;
			}
		}
	}
}
.menu-main-menu-container-outer {
	display: flex;
	align-items: center;
}
.menu-item{
	a{
		position: relative;
		&:before{
			position: absolute;
			content: "";
			background: $primaryYellow;
			top: 0;
			left: 0;
			width: 100%;
  		-webkit-transform: scaleX(0);
  		        transform: scaleX(0);
			height: 2px;
			-webkit-transition: -webkit-transform 0.2s ease-out;
			transition: -webkit-transform 0.2s ease-out;
			transition: transform 0.2s ease-out;
			transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
			-webkit-transform-origin: top right;
			        transform-origin: top right;
		}
		&:after{
			position: absolute;
			content: "";
			background: $primaryYellow;
			bottom: 0;
			right: 0;
			width: 100%;
  		-webkit-transform: scaleX(0);
  		        transform: scaleX(0);
			height: 2px;
			-webkit-transition: -webkit-transform 0.2s ease-out;
			transition: -webkit-transform 0.2s ease-out;
			transition: transform 0.2s ease-out;
			transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
			-webkit-transform-origin: bottom left;
			        transform-origin: bottom left;
		}
		&:hover{
			&:before{
				-webkit-transform-origin: top left;
				        transform-origin: top left;
				-webkit-transform: scaleX(1);
				        transform: scaleX(1);
			}
			&:after{
				-webkit-transform-origin: bottom right;
				        transform-origin: bottom right;
				-webkit-transform: scaleX(1);
				        transform: scaleX(1);
			}
		}
		&:focus{
			&:before{
				-webkit-transform-origin: top left;
				        transform-origin: top left;
				-webkit-transform: scaleX(1);
				        transform: scaleX(1);
			}
			&:after{
				-webkit-transform-origin: bottom right;
				        transform-origin: bottom right;
				-webkit-transform: scaleX(1);
				        transform: scaleX(1);
			}
		}
	}
}
